.order {
  display: none;
}
.selected-items {
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid rgba(0, 0, 0, 0.5);
  border-radius: 0.25rem;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  background-color: #f0f0f0;
  z-index: 999;
  overflow: hidden;
}
.order-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  border-radius: 0.25rem;
  background-color: #fff;
}

.order-container > h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  /* letter-spacing: 0.25rem; */
  letter-spacing: 0.1rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}
.selected-items-info::-webkit-scrollbar,
.order-item-center::-webkit-scrollbar {
  display: none;
}
.selected-items-info,
.order-item-center {
  -ms-overflow-style: none; /* 인터넷 익스플로러 */
  scrollbar-width: none; /* 파이어폭스 */
}
.selected-items-info {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr;
  padding: 0.2rem;
  border-radius: 0.25rem;
  overflow: auto;
}
.order-item {
  display: grid;
  grid-auto-rows: 100%;
  border-radius: 0.25rem;
  border: 1px solid rgba(0, 0, 0, 0.3);
}
.order-img {
  margin: auto;
  border-radius: 0.25rem;
}

.order-item-info {
  display: flex;
  flex-direction: column;
  border: 0.25rem;
}
.order-item-title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.order-item-title > span {
  text-transform: capitalize;
}
.order-item-center {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
  border: 0.25rem;
}
.order-row {
  display: flex;
  gap: 4px;
  margin-bottom: 0.125rem;
}
.order-row h3 {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.order-row p {
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: capitalize;
}
.selected-item-price {
  display: flex;
  flex-direction: column;
  margin-bottom: 0.25rem;
}
.selected-item-price h3 {
  flex: 0 0 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}
.selected-item-btn {
  display: flex;
  justify-content: center;
  padding: 0.3rem;
}
.selected-item-btn button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e0e0e0;
  border-color: transparent;
  border-radius: 2.5rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s linear;
  color: #000;
}
.selected-item-btn button:hover {
  background-color: #000;
  color: #fff;
}

